var MapboxDraw = require('@mapbox/mapbox-gl-draw');

var createSupplementaryPoints = require('@mapbox/mapbox-gl-draw/src/lib/create_supplementary_points');

var moveFeatures = require('@mapbox/mapbox-gl-draw/src/lib/move_features');

var Constants = require('@mapbox/mapbox-gl-draw/src/constants');

var constrainFeatureMovement = require('@mapbox/mapbox-gl-draw/src/lib/constrain_feature_movement');

var distance = require('@turf/distance').default;

var turfHelpers = require('@turf/helpers');

var circle = require('@turf/circle').default;

var createSupplementaryPointsForCircle = require('../utils/create_supplementary_points_circle');

var DirectModeOverride = MapboxDraw.modes.direct_select;

DirectModeOverride.dragFeature = function (state, e, delta) {
  moveFeatures(this.getSelected(), delta);
  this.getSelected().filter(function (feature) {
    return feature.properties.isCircle;
  }).map(function (circle) {
    return circle.properties.center;
  }).forEach(function (center) {
    center[0] += delta.lng;
    center[1] += delta.lat;
  });
  state.dragMoveLocation = e.lngLat;
};

DirectModeOverride.dragVertex = function (state, e, delta) {
  if (state.feature.properties.isCircle) {
    var center = state.feature.properties.center;
    var movedVertex = [e.lngLat.lng, e.lngLat.lat];
    var radius = distance(turfHelpers.point(center), turfHelpers.point(movedVertex), {
      units: 'kilometers'
    });
    var circleFeature = circle(center, radius);
    state.feature.incomingCoords(circleFeature.geometry.coordinates);
    state.feature.properties.radiusInKm = radius;
  } else {
    var selectedCoords = state.selectedCoordPaths.map(function (coord_path) {
      return state.feature.getCoordinate(coord_path);
    });
    var selectedCoordPoints = selectedCoords.map(function (coords) {
      return {
        type: Constants.geojsonTypes.FEATURE,
        properties: {},
        geometry: {
          type: Constants.geojsonTypes.POINT,
          coordinates: coords
        }
      };
    });
    var constrainedDelta = constrainFeatureMovement(selectedCoordPoints, delta);

    for (var i = 0; i < selectedCoords.length; i++) {
      var coord = selectedCoords[i];
      state.feature.updateCoordinate(state.selectedCoordPaths[i], coord[0] + constrainedDelta.lng, coord[1] + constrainedDelta.lat);
    }
  }
};

DirectModeOverride.toDisplayFeatures = function (state, geojson, push) {
  if (state.featureId === geojson.properties.id) {
    geojson.properties.active = Constants.activeStates.ACTIVE;
    push(geojson);
    var supplementaryPoints = geojson.properties.user_isCircle ? createSupplementaryPointsForCircle(geojson) : createSupplementaryPoints(geojson, {
      map: this.map,
      midpoints: true,
      selectedPaths: state.selectedCoordPaths
    });
    supplementaryPoints.forEach(push);
  } else {
    geojson.properties.active = Constants.activeStates.INACTIVE;
    push(geojson);
  }

  this.fireActionable(state);
};

module.exports = DirectModeOverride;