var MapboxDraw = require('@mapbox/mapbox-gl-draw');

var createSupplementaryPoints = require('@mapbox/mapbox-gl-draw/src/lib/create_supplementary_points');

var moveFeatures = require('@mapbox/mapbox-gl-draw/src/lib/move_features');

var Constants = require('@mapbox/mapbox-gl-draw/src/constants');

var createSupplementaryPointsForCircle = require('../utils/create_supplementary_points_circle');

var SimpleSelectModeOverride = MapboxDraw.modes.simple_select;

SimpleSelectModeOverride.dragMove = function (state, e) {
  // Dragging when drag move is enabled
  state.dragMoving = true;
  e.originalEvent.stopPropagation();
  var delta = {
    lng: e.lngLat.lng - state.dragMoveLocation.lng,
    lat: e.lngLat.lat - state.dragMoveLocation.lat
  };
  moveFeatures(this.getSelected(), delta);
  this.getSelected().filter(function (feature) {
    return feature.properties.isCircle;
  }).map(function (circle) {
    return circle.properties.center;
  }).forEach(function (center) {
    center[0] += delta.lng;
    center[1] += delta.lat;
  });
  state.dragMoveLocation = e.lngLat;
};

SimpleSelectModeOverride.toDisplayFeatures = function (state, geojson, display) {
  geojson.properties.active = this.isSelected(geojson.properties.id) ? Constants.activeStates.ACTIVE : Constants.activeStates.INACTIVE;
  display(geojson);
  this.fireActionable();
  if (geojson.properties.active !== Constants.activeStates.ACTIVE || geojson.geometry.type === Constants.geojsonTypes.POINT) return;
  var supplementaryPoints = geojson.properties.user_isCircle ? createSupplementaryPointsForCircle(geojson) : createSupplementaryPoints(geojson);
  supplementaryPoints.forEach(display);
};

module.exports = SimpleSelectModeOverride;